.result {
    color: teal;
    font-size: 1.5em;
    text-align: center;
    background-color: aliceblue;
    box-shadow: 5px 5px 10px 0px hsl(120, 22%, 35%);
    border: 2px solid teal;
    border-radius: 20px;
    padding: 0px 20px;
}

.result__value {
    color: hsl (180, 100%, 15%);
    font-weight: 700;
}